footer {
    margin: auto;
    align-items: center;
    padding: 0 0 .5em 0;
    display: flex;
    justify-content: center;
    width: 100%;
    vertical-align: text-top;
    gap: .5em;
    bottom: 0;
    max-height: 10vh;
}

footer img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    filter: invert(50%);
    transition: all .5s;
}

footer img:hover {
    opacity: 1;
    box-shadow: 1px 1px 4px rgb(75, 75, 75);
    filter: invert(25%);
}