*, *::before, *::after {
  padding: 0;
  margin: 0;
  font-family: 'Athiti', sans-serif;
  box-sizing: border-box;
}

a, h2, p {
  font-family: 'Athiti', sans-serif;
}

a {
  text-decoration: none;
  font-weight: 700;
  color: #888;
}

a:hover {
   color: #416c48;
}

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0;
    padding: 0;
    background: #efefef;
    max-width: 100%;
}

#container {
    min-height: 100vh;
    height: 100px;
}

section.section-about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    max-width: 100vw;
    width: 100%;
    gap: 3rem;
}


@media screen and (min-width: 500px) {
    section.section-about {
        flex-direction: row;
    }
    header li:last-child {
        margin-right: 1.5rem;
    }
    header {
    justify-content: flex-end;
    }
}